<script>
import common from './common'
export default {
  mixins: [common],
  data () {
    return {
      gameType: 'PK10',
      menus: [
        {
          title: 'PK10',
          groups: [
            {
              // title: 'PK10',
              items: [
                {id: '-1-1-1-PK10', title: '', description: '01-101011', example: ''},
                {id: '-1-1-2-PK10', title: '', description: ' 01-10102102', example: ''},
                {id: '-1-1-3-PK10', title: '', description: ' "01-10103103', example: ''},
                {id: '-1-1-4-PK10', title: '', description: ' "01-10104104', example: ''},
                {id: '-1-1-5-PK10', title: '', description: ' 01-10105105', example: ''},
                {id: '-1-1-6-PK10', title: '', description: '', example: ''}
              ]
            }
          ]
        }
      ],
      // 
      type: {
        id: '-1-1-1-PK10',
        // 
        title: '',
        // 
        description: ''
      },
      getTitle () {
        switch (this.type.id.slice(0, 2)) {
          case '-1':
            return 'PK10'
        }
      }
    }
  }
}
</script>



