<template lang="jade">

  .order-list.history

    .banner(style="padding: 15px; color: black; font-weight: bold") 

    el-table.ghost(:data="data" v-bind:row-class-name="tableRowClassName" v-on:row-click="setSelected")

      el-table-column(prop="cnname" label="")

      el-table-column(prop="beginissue" label="")

      el-table-column(label="" width="200" show-overflow-tooltip=true)
        template(scope="scope") {{ scope.row.methodname  + '[' + scope.row.codes + '] ' }}

      el-table-column(label="" width="80")
        template(scope="scope") {{ scope.row.modes === 1 ? '': scope.row.modes === 2 ? '': scope.row.modes === 3 ? '': '' }}


      el-table-column(prop="taskprice" label="" width="120" align="right")

      el-table-column(label="" width="100" align="right")
        template(scope="scope") {{ scope.row.status === 0 ? '': scope.row.modes === 1 ? '': '' }}


</template>

<script>
  import api from '../http/api'
  export default {
    data () {
      return {
        /*
          "beginissue": "161227033",//
          "begintime": "2016-12-27 11:30:23.0",//
          "cnname": "",//
          "codes": "0123",//
          "finishprice": 180,//
          "lotteryid": 1,//
          "methodid": 29,//
          "methodname": "",//
          "modes": 1,//, 1-2-3-4-
          "status": 2,//(0:;1:;2:)
          "taskid": 56,//id
          "taskprice": 180,//
          "userid": 4,//id
          "username": "steven"//
        */
        data: []
      }
    },
    computed: {
    },
    created () {
      this.__getFollowList()
    },
    activated () {
      this.__getFollowList()
    },
    methods: {
      __getFollowList () {
        this.$http.post(api.getFollowList, {size: 5}).then(({data}) => {
          // success
          if (data) this.data = data
        }, (rep) => {
          // error
        })
      },
      tableRowClassName (row, index) {
        if (row.selected) return 'selected-row'
      },
      setSelected (row) {
        !row._empty && (row.selected = !row.selected)
      }
    }
  }
</script>

<style lang="stylus">
  H = 1.6rem
  .order-list
    .el-table
    // width "calc(100% - %s)" % s
      .el-table__body-wrapper
        // padding-right .3rem
        max-height H
      
</style>

<style lang="stylus" scoped>
  @import '../var.stylus'
</style>
  
 




