<script>
import common from './common'
export default {
  mixins: [common],
  data () {
    return {
      gameType: 'K3',
      menus: [
        // {id: '-1-1-1-K3', title: '', description: '3-1811. ', example: ''},
        {
          title: '',
          groups: [
            {
              items: [
                {id: '2-1-1-K3', title: '', description: '(11-66)', example: ''},
                {id: '2-1-2-K3', title: '', description: '', example: ''}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              items: [
                {id: '-2-1-1-K3', title: '', description: '', example: ''},
                {id: '-2-1-2-K3', title: '', description: '1-6211', example: ''}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              items: [
                {id: '3-1-1-K3', title: '', description: '111222666', example: ''},
                {id: '3-1-2-K3', title: '', description: '111222666', example: ''}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              items: [
                {id: '-3-1-1-K3', title: '', description: '1-633', example: ''},
                {id: '-3-1-2-K3', title: '', description: '1-623', example: ''}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              items: [
                {id: '+3-1-1-K3', title: '', description: '', example: ''},
                {id: '+3-1-2-K3', title: '', description: '123234345456', example: ''}
              ]
            }
          ]
        },
        {
          title: '',
          groups: [
            {
              items: [
                {id: '0-1-1-K3', title: '', description: '3-1811. ', example: ''},
                {id: '0-1-2-K3', title: '1', description: '', example: ''}
              ]
            }
          ]
        }
        // {id: '-4-1-1-K3', title: '1', description: '', example: ''}
      ],
      // 
      type: {
        id: '2-1-1-K3',
        // 
        title: '',
        // 
        description: ''
      },
      lucknumbers: [2, 4, 5],
      getTitle () {
        switch (this.type.id.slice(0, 2)) {
          case '2-':
            return ''
          case '-2':
            return ''
          case '+3':
            return ''
          case '3-':
            return ''
          case '-3':
            return ''
          case '-4':
            return ''
          case '0-':
            return ''
        }
      }
    }
  }
}
</script>



