<template lang="jade">
  .game-selection

    GameNumberRow(v-for="(row, i) in rows" v-bind:row="row"  v-on:numbers-change="numbersChange" v-bind:titleSpan="titleSpan" v-on:select = "select")
    
    el-row(v-if="rows.length === 0")
      el-col(:span="20")
        el-input(v-model="value" type="textarea" autofocus  v-bind:autosize="{ minRows: 5, maxRows: 10 }" placeholder=" [ ][,]  [;] ")
      el-col.btn-groups(:span="4")
        .ds-button.outline(@click="removeRepeat") 
        br
        .ds-button.outline(v-bind:class="{disabled: !upload}") {{ upload ? '' : '' }}
          input(type="file" @change="selectFiles" multiple v-if="upload")
        br
        .ds-button.outline(@click="value = ''") 
    el-row.pos(v-if="show")
      el-col(v-bind:span="13")
        label.ds-checkbox-label(v-for="p in positions" @click="p.selected = !p.selected" v-bind:class="{active: p.selected}") 
          span.ds-checkbox 
          | {{ p.title }}
      el-col.notice(:span="11")
        |  
        span.count {{ psl }}
        |    
        span.comb {{ comb }}
        |  
</template>

<script>
