          /***
          ** [,] | [[,]] | [] | [] | [] | [*[]]
          ***/
          {ids: '5-1-1, 5-1-3, 4-3-1, 4-3-3, -3-1-1, 2-3-1, 1-1-1, -1-1-1, -1-2-1, -1-3-1', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},
          /***
