          ***/
          {ids: '+3-3-1, 3-3-1, -3-3-1', title: '', min: 0, max: 26},
          /***
          ** [] | [] | [] | [] | []
          ***/
          {ids: '+3-3-2, 3-3-2, -3-3-2, 2-2-3, 2-4-3', title: '', min: 0, max: 9},
          /***
          ** [] | [] | []
          ***/
          {ids: '+3-3-3, 3-3-3, -3-3-3', title: '', min: 0, max: 9},
          /***
          ** [] | [] | []          ***/
          {ids: '+3-3-4, 3-3-4, -3-3-4', title: '', values: [{selected: false, title: '', value: '1'}, {selected: false, title: '', value: '2'}, {selected: false, title: '', value: '3'}]},
          /***
          ** [] | [] | [] | []
          ***/
          {ids: '2-2-1, 2-4-1, -1-1-3', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},
          /***
          ** 
          ***/
          {ids: '0-1-1, 0-1-2, 0-1-3, 0-1-4, 0-1-5, 0-1-6, 0-2-1, 0-2-2, 0-2-3, 0-2-4, 0-3-1, 0-3-2', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},
          /***
