          ***/
          {ids: '-2-1-1', title: '', values: [{selected: false, title: '', value: '1'}, {selected: false, title: '', value: '2'}, {selected: false, title: '', value: '3'}, {selected: false, title: '', value: '4'}], buttons: ['']},
          /***
          ** []
          ***/
          {ids: '-2-1-1', title: '', values: [{selected: false, title: '', value: '1'}, {selected: false, title: '', value: '2'}, {selected: false, title: '', value: '3'}, {selected: false, title: '', value: '4'}], buttons: ['']},
           /***
          ** []
          ***/
          {ids: '-2-1-2', title: '', values: [{selected: false, title: '', value: '1'}, {selected: false, title: '', value: '2'}, {selected: false, title: '', value: '3'}, {selected: false, title: '', value: '4'}], buttons: ['']},
          /***
          ** []
          ***/
          {ids: '-2-1-2', title: '', values: [{selected: false, title: '', value: '1'}, {selected: false, title: '', value: '2'}, {selected: false, title: '', value: '3'}, {selected: false, title: '', value: '4'}], buttons: ['']},
          /***
          ** []
          ***/
          {ids: '-2-2-1', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},
          {ids: '-2-2-2', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},
          {ids: '-2-2-3', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},
          {ids: '-2-2-4', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},
          {ids: '-2-2-5', title: '', min: 0, max: 9, buttons: ['', '', '', '', '', '']},

          // =========================================================115========================================================
          /***
          **
          ***/
          {ids: '1-1-1-115, 3-1-1-115, 3-2-1-115, 2-1-1-115', title: '', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '1-1-1-115, 3-1-1-115, 3-2-1-115, 2-1-1-115', title: '', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '1-1-1-115, 3-1-1-115, 3-2-1-115', title: '', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '3-1-3-115, 2-1-3-115', title: '', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '3-1-5-115:2, 2-1-5-115:1, -1-3-1-115:1, -1-3-2-115:2, -1-3-3-115:3, -1-3-4-115:4, -1-3-5-115:5, -1-3-6-115:6, -1-3-7-115:7', title: '', min: 1, max: 11, l: 2},
          {ids: '3-1-5-115, 2-1-5-115, -1-3-1-115, -1-3-2-115, -1-3-3-115, -1-3-4-115, -1-3-5-115, -1-3-6-115, -1-3-7-115', title: '', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '0-1-1-115', title: '', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-1-115', title: '11', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-2-115', title: '22', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-3-115', title: '33', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-4-115', title: '44', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-5-115', title: '55', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-6-115', title: '65', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-7-115', title: '75', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-8-115', title: '85', min: 1, max: 11, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-2-1-1-115', class: 'default square', values: [{selected: false, title: '50', value: '1'}, {selected: false, title: '51', value: '2'}, {selected: false, title: '32', value: '3'}, {selected: false, title: '23', value: '4'}, {selected: false, title: '41', value: '5'}, {selected: false, title: '50', value: '6'}]},
          {ids: '-2-1-2-115', title: '', min: 3, max: 9, buttons: ['', '', '', '', '', '']},

          // =========================================================PK10========================================================
          /***
          **
          ***/
          {ids: '-1-1-1-PK10, -1-1-2-PK10, -1-1-3-PK10, -1-1-4-PK10, -1-1-5-PK10', class: 'ds-icon-PK10', title: '', min: 1, max: 10, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-2-PK10, -1-1-3-PK10, -1-1-4-PK10, -1-1-5-PK10', class: 'ds-icon-PK10', title: '', min: 1, max: 10, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-3-PK10, -1-1-4-PK10, -1-1-5-PK10', class: 'ds-icon-PK10', title: '', min: 1, max: 10, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-4-PK10, -1-1-5-PK10', class: 'ds-icon-PK10', title: '', min: 1, max: 10, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '-1-1-5-PK10', class: 'ds-icon-PK10', title: '', min: 1, max: 10, l: 2, buttons: ['', '', '', '', '', '']},

          // =========================================================KL8========================================================
          /***
          **
          ***/
          {ids: '-1-1-1-KL8, -1-1-2-KL8, -1-1-3-KL8, -1-1-4-KL8, -1-1-5-KL8, -1-1-6-KL8, -1-1-7-KL8', min: 1, max: 80, l: 2, buttons: ['', '', '', '', '', '', '', ''], btnClass: 'block'},

          // =========================================================KL8========================================================
          /***
          **
          ***/
          {ids: '0-1-1-K3', title: '', min: 3, max: 18, l: 2, buttons: ['', '', '', '', '', '']},
          {ids: '2-1-1-K3, 2-1-2-K3', class: 'dice double-width', title: '', values: [{selected: false, title: [1, 1], value: 1}, {selected: false, title: [2, 2], value: 2}, {selected: false, title: [3, 3], value: 3}, {selected: false, title: [4, 4], value: 4}, {selected: false, title: [5, 5], value: 5}, {selected: false, title: [6, 6], value: 6}]},
          {ids: '2-1-2-K3', class: 'dice', title: '', values: [{selected: false, title: [1], value: 1}, {selected: false, title: [2], value: 2}, {selected: false, title: [3], value: 3}, {selected: false, title: [4], value: 4}, {selected: false, title: [5], value: 5}, {selected: false, title: [6], value: 6}]},
          {ids: '-2-1-1-K3, -3-1-1-K3', class: 'dice', title: '', values: [{selected: false, title: [1], value: 1}, {selected: false, title: [2], value: 2}, {selected: false, title: [3], value: 3}, {selected: false, title: [4], value: 4}, {selected: false, title: [5], value: 5}, {selected: false, title: [6], value: 6}]},
          {ids: '-2-1-2-K3, -3-1-2-K3', class: 'dice', title: '', values: [{selected: false, title: [1], value: 1}, {selected: false, title: [2], value: 2}, {selected: false, title: [3], value: 3}, {selected: false, title: [4], value: 4}, {selected: false, title: [5], value: 5}, {selected: false, title: [6], value: 6}]},
          {ids: '-2-1-2-K3, -3-1-2-K3', class: 'dice', title: '', values: [{selected: false, title: [1], value: 1}, {selected: false, title: [2], value: 2}, {selected: false, title: [3], value: 3}, {selected: false, title: [4], value: 4}, {selected: false, title: [5], value: 5}, {selected: false, title: [6], value: 6}]},
          {ids: '3-1-1-K3', class: 'dice double-width', title: '', values: [{selected: false, title: [1, 1, 1], value: 1}, {selected: false, title: [2, 2, 2], value: 2}, {selected: false, title: [3, 3, 3], value: 3}, {selected: false, title: [4, 4, 4], value: 4}, {selected: false, title: [5, 5, 5], value: 5}, {selected: false, title: [6, 6, 6], value: 6}]},
          {ids: '3-1-2-K3, +3-1-2-K3', class: 'dice', title: '', values: [{selected: false, title: [''], value: 1}]},
          {ids: '+3-1-1-K3', class: 'dice double-width', title: '', values: [{selected: false, title: [1, 2, 3], value: 1}, {selected: false, title: [2, 3, 4], value: 2}, {selected: false, title: [3, 4, 5], value: 3}, {selected: false, title: [4, 5, 6], value: 4}]}

        ],
        // 
        value: '',
        // 
        positions: [
          {
            title: '',
            value: 5,
            selected: false
          },
          {
            title: '',
            value: 4,
            selected: false
          },
          {
            title: '',
            value: 3,
            selected: true
          },
          {
            title: '',
            value: 2,
            selected: true
          },
          {
            title: '',
            value: 1,
            selected: true
          }
        ],
        // pos
        allChecks: [
          // 2
          {ids: '-1-1-2, -1-1-3, -1-1-4', min: 2},
          // 3
          {ids: '-1-2-2, -1-2-3, -1-2-4, -1-2-5, -1-2-6, -1-2-7', min: 3},
          // 4
          {ids: '-1-3-2, -1-3-3, -1-3-4, -1-3-5, -1-3-6', min: 4}
        ],
        ids: '-1-1-2, -1-1-3, -1-1-4, -1-2-2, -1-2-3, -1-2-4, -1-2-5, -1-2-6, -1-2-7, -1-3-2, -1-3-3, -1-3-4, -1-3-5, -1-3-6',
        // 
        ns: [],
        // 
        upload: true,
        titleSpan: 0
      }
    },
    computed: {
      // 
      nr () {
        return ['3-1-5-115', '2-1-5-115', '-1-3-1-115', '-1-3-2-115', '-1-3-3-115', '-1-3-4-115', '-1-3-5-115', '-1-3-6-115', '-1-3-7-115'].indexOf(this.type.id) !== -1
      },
      // 
      rows () {
        return this.allRows.filter(row => {
          let matchid = ((' ' + row.ids + ',').match(
            new RegExp(this.type.id.match(/^[+-]/) ? ('\\' + this.type.id + '(:\\d)*,') : '[^+-]' + this.type.id + '(:\\d)*,', 'g')
          ) || [])[0]
          if (matchid) {
            this.$set(row, 'id', matchid)
          }
          return matchid
        })
      },
      // 
      show () {
        return this.ids.match(new RegExp('[^+-]*' + (this.type.id.match(/^[+-]/) ? ('\\' + this.type.id) : this.type.id), 'g'))
      },
      // 
      ps () {
        return this.positions.filter(p => p.selected).map(p => {
          return (p = p.value)
        })
      },
      // 
      psl () {
        return this.positions.filter(p => p.selected).length
      },
      // 
      nsl () {
        return this.ns.map(n => {
          return (n = n.length)
        })
      },
      // 
      r () {
        return this.ns.join(',').split(',').filter(n => n !== '').reduce((p, n) => {
          if (p[n] !== undefined) p.repeat += 1
          else p[n] = n
          return p
        }, {repeat: 0}).repeat
      },
      // 
      n () {
        return N[this.type.id] ? N[this.type.id]({
          ns: this.ns,
          nsl: this.nsl,
          ps: this.ps,
          psl: this.psl,
          value: this.value,
          r: this.r
        }) : 0
      },
      // 
      comb () {
        let min = this.allChecks.filter(check => check.ids.match(
          new RegExp('[^+-]*' + (this.type.id.match(/^[+-]/) ? ('\\' + this.type.id) : this.type.id), 'g')
        ))[0]
        return min ? C(this.positions.filter(p => p.selected).length, min.min) : 0
      }
    },
    watch: {
      n () {
        this.$emit('n-change', this.n)
      },
      value () {
        this.value = this.value.replace(/[^0-9,;\s]+/g, '').replace(/[,;\s]+/g, ' ')
        this.$emit('set-nsns', this.value ? this.value.trim().replace(/\s{1,}/g, '|') : '')
      },
      rows () {
        this.titleSpan = this.rows.reduce((p, r) => {
          if (r.title) p = Math.max(p, r.title.length - Math.ceil((r.title.match(/\d/g) || []).length / 2))
          return p
        }, 0)
      },
      ns () {
        this.$emit('set-nsns', this.ns.join('|'))
      },
      ps () {
        this.$emit('set-ps', this.ps)
      }
    },
    created () {
      if (!(window.File && window.FileReader && window.FileList && window.Blob)) this.upload = false
    },
    methods: {
      // 
      numbersChange () {
        this.ns = this.rows.map(r => {
          return (r = r.ns || [])
        })
      },
      // on number row selecting
      select ({args}) {
        if (this.nr) {
          this.__setCall({
            fn: '__unselectSelectedNumber',
            args: args
          })
        }
      },
      selectFiles (evt) {
        let allowedFiles = 'text/plain'
        let files = evt.target.files
        Array.from(files).forEach(f => {
          if (f.type.indexOf(allowedFiles) !== -1) {
            let reader = new window.FileReader()
            // reader.onerror = this.error
            // reader.onprogress = this.progress
            // reader.onabort = this.abort
            // reader.onloadstart = this.loadstart
            reader.onload = this.load
            reader.readAsText(f, 'utf-8')
          }
        })
      },
      load (evt) {
        // console.log(evt.target.result)
        this.value += evt.target.result
        // .replace(/\s+/g, ' ')
      },
      removeRepeat () {
        this.value = removeDuplicate(this.value, ' ')
      }
    },
    components: {
      GameNumberRow
    }
  }
</script>
<style lang="stylus">
  .el-textarea 
    box-sizing border-box
    textarea
      resize none
      // min-height 1.06rem
</style>
<style lang="stylus" scoped>
  @import '../var.stylus'
  .game-selection
    padding PW 0
    .pos
      padding-left .3rem
  .el-textarea
    padding 0 .1rem 0 PWX
    
  .el-col
    min-height GCH
    line-height GCH
    &.btn-groups
      line-height 0
      .ds-button
        margin-bottom .085rem
        position relative
        &:last-child
          margin-bottom 0
        input
          position absolute
          top 0 
          left 0 
          right 0 
          bottom 0
          opacity 0
          width 100%
          
    &.notice
      font-size .12rem
      color #999
      line-height .2rem
      padding PW PWX
  .count
  .comb
    color DANGER
  
</style>



