<template lang="jade">

  .order-list.history

    .banner(style="padding: 15px; color: black; font-weight: bold") 

    el-table.ghost(:data="data" v-bind:row-class-name="tableRowClassName" v-on:row-click="setSelected")

      el-table-column(prop="issue" label="")

      el-table-column(label="" width="200" show-overflow-tooltip=true)
        template(scope="scope") {{ scope.row.methodname  + '[' + scope.row.code + '] ' }}


      el-table-column(label="" width="80")
        template(scope="scope") {{ scope.row.modes === 1 ? '': scope.row.modes === 2 ? '': scope.row.modes === 3 ? '': '' }}

      el-table-column(prop="multiple" label="" width="80" align="right")

      el-table-column(prop="totalprice" label="" width="120" align="right")

      el-table-column(prop="bonus" label="" width="100" align="right")

</template>

<script>
  import api from '../http/api'
  export default {
    data () {
      return {
        /*
          "bonus": 0,//
          "code": "0123",//
          "isgetprize": 2,//0:;1:;2:)
          "issue": "161227039",//
          "methodid": 29,//
          "methodname": "",//
          "modes": 1,//,1-2-3-4-
          "multiple": 1,//
          "projectid": 371,//id
          "totalprice": 20,//
          "userid": 4,//id
          "username": "steven",//
          "writetime": "2016-12-29 17:29:09"//
        */
        data: []
      }
    },
    computed: {
    },
    created () {
      this.__getOrderList()
    },
    activated () {
      this.__getOrderList()
    },
    methods: {
      __getOrderList () {
        this.$http.post(api.getOrderList, {size: 5}).then(({data}) => {
          // success
          if (data && data[0]) this.data = data
        }, (rep) => {
          // error
        })
      },
      tableRowClassName (row, index) {
        if (row.selected) return 'selected-row'
      },
      setSelected (row) {
        !row._empty && (row.selected = !row.selected)
      }
    }
  }
</script>

<style lang="stylus">
  H = 1.6rem
  .order-list
    .el-table
    // width "calc(100% - %s)" % s
      .el-table__body-wrapper
        // padding-right .3rem
        max-height H
      
</style>

<style lang="stylus" scoped>
  @import '../var.stylus'
</style>
  
 




